import React, { ReactElement } from 'react';
import { EditableField } from '../components/';
import { TournamentSettings, TournamentStatus, Player } from '../interfaces';
import url from 'url';

import styles from '../css/profile.module.css';
import { server } from '../utils/requests';
import { defaultSettings, TournamentSetting } from '../assets/settings';
import { capitalise } from '../utils/prototype';
import { Bottom } from '../components/Buttons';
import { IoMdClose } from 'react-icons/io';
import CollapsableSection, { SectionBreak } from '../components/CollapsableSection';

export default class Settings extends React.Component<{
	updateTournamentSettings: () => Promise<void>
	updateSetting: (k: string, v: any) => Promise<void>
	updateRounds: () => Promise<void>
	status: TournamentStatus
	settings: TournamentSettings
	toggleSettings: () => void
	setLastRound: (override: boolean) => void // need to include a method to cancel a last round token when the total rounds is incremented
	triggerError: (message: string) => void
	players: Player[]
	rounds: Date[]
}, {
	sectionExpando: string;
}> {

	public state = {
		sectionExpando: 'general'
	}

	handleToggle = (k: string, v: any, def: {
		name?: string,
		enum?: string[],
		convert?: (v: any) => string,
		type?: string,
		locked?: boolean,
		lockOnInit?: boolean
	}) => {
		try {
			let value: any;
			if (def.enum) {
				let index = def.enum.indexOf(v);
				if (index === -1) console.error('Invalid setting value ' + k + ': ' + v);
				let setIndex = (index + 1) % def.enum.length;
				value = def.enum[setIndex];
			} else
			if (def.type === 'boolean') {
				value = !v;
			} else
			if (def.type === 'number') {

				if (v === 'Infinity') value = Infinity as number;
				else if (v === '') value = Infinity as number;
				else v = Number(v) as number;

				if (k === 'totalRounds') {
					if (v <= this.props.status.round) throw new Error('Can\'t make total number of rounds less than current round number');
					if (v !== Infinity && v >= 100) throw new Error('Can\'t make total number of rounds more than 100');
				}
				
				return v;
			}
			this.props.updateSetting(k, value);
		} catch (e) {
			if (e && e.message) this.props.triggerError(e.message);
		}
	}

	private modifyHeightValues = (): void => {
		let headers = Array.from(document.querySelectorAll('.' + [styles.sectionListWrapper].join('.'))) as HTMLDivElement[];
		for (let h of headers) {
			let list = h.firstElementChild as HTMLDivElement;
			h.style.height = h.className.includes(styles.expanded) ? list.getBoundingClientRect().height + 'px' : '0px';
		}
	}

	private watchProps = Object.values(defaultSettings).reduce((acc, curr) => {
		for (let def of Object.values(curr)) {
			if (def.show) acc.push(...Object.keys(def.show));
		}
		return acc;
	}, []) as (keyof TournamentSettings)[];
	componentDidUpdate(prevProps: { settings: TournamentSettings}, prevState: { sectionExpando: string }) {
		for (let k of this.watchProps) {
			if (prevProps.settings[k] !== this.props.settings[k]) this.modifyHeightValues();
		}
	}
    
    renderRound = (i: number) => {
        return (
            <div className={[styles.row, styles.roundRow].join(' ')} key={['row', i].join('.')}>
                <div className='fieldBox'>
                    {`Round ${i}:`}
                </div>
                <EditableField
                    handleSubmit={this.props.updateSetting}
                    content={this.props.rounds[i] ? this.props.rounds[i].toString() : ''}
                    defaultEditContent={new Date(Date.now()).toISOString()}
                    url={url.resolve(server, ['tournament', this.props.status.id, 'updateRound'].join('/'))}
                    inputType='datetime-local'
                    onSubmitSuccess={(e) => {
                        this.props.updateRounds();
                        return e;
                    }}
                    additionalStyles={{
                        editableField: styles.editableField,
                        input: styles.input,
                    }}
                    additionalInputProps={{}}
                    name={i.toString()}
                    show={{
                        submit: true,
                        increment: true
                    }}
                />
            </div>
        );
    }

	renderEntry = (([k, def]: [keyof TournamentSettings, TournamentSetting], i: number): ReactElement => {
        let hidden = false;
        if (def.show) {
            for (let [k, v] of Object.entries(def.show)) {
                let value = this.props.settings[k as keyof TournamentSettings];
                if (value !== v) hidden = true;
            }
        }
        
		let locked = false;
		if (def.locked) locked = true;
        else if (def.lockOnInit && this.props.status.round) locked = true;
        
        let v = this.props.settings[k as keyof TournamentSettings] ?? def.defaultValue;
		let content = def.convert && !def.value ? def.convert(v) : v as string;
		let displayContent = typeof content !== 'undefined' ? content.toString() : '\u200b';
		return (
			<div className={styles.row} key={['row', i].join('.')} style={{
				height: def.show ? hidden ? '0px' : '29px' : undefined
			}}>
				<div className={['fieldBox', def.show ? styles.semiPresent : ''].join(' ')}>
					{def.name}
					{def.name.endsWith('?') ? '' : ':'}
				</div>
				{(() => {
					if (locked || def.value) {
						return <div className={['fieldBox', styles.locked].join(' ')}>
							{def.value ?def.convert(def.value(this.props.status, this.props.players)) : displayContent}
						</div>
					} else
					if (def.type === 'number') {
						return <EditableField
							handleSubmit={this.props.updateSetting}
							content={displayContent}
							url={url.resolve(server, ['tournament', this.props.status.id, 'updateSetting'].join('/'))}
							inputType='number'
							onSubmitSuccess={(e) => {
								this.props.updateTournamentSettings();
								return e;
							}}
							additionalStyles={{
								editableField: styles.editableField,
								input: styles.input
							}}
							name={k}
							additionalInputProps={def.nb}
							show={{
								submit: true,
								increment: true,
								...def.allow
							}}
						/>
					} else {
						return <div
							className={['fieldBox', styles.editable].join(' ')}
							onClick={() => this.handleToggle(k, v, def)}
						>
							{displayContent}
						</div>
					}
				})()}
			</div>
		);
	});

	render(): ReactElement {
		return (
			<div className={styles.sidebar}>
				<div className={styles.profile} >				
					<div className='topBanner'>
						Settings
					</div>
					{Object.entries(defaultSettings).map(([section, data], i): ReactElement => {
                        return (
                            <CollapsableSection
                                key={section}
                                value={section}
                                header={capitalise(section)}
                                expanded={this.state.sectionExpando === section}
                                setExpanded={(state: boolean, action: string) => this.setState({
                                    sectionExpando: state ? action : ''
                                })}
                            >
                                {(Object.entries(data) as [keyof TournamentSettings, TournamentSetting][])
                                    .map(this.renderEntry)}
                            </CollapsableSection>
                        )
					})}
					{(() => {
						let entries = [];
						let length = this.props.settings.totalRounds === Infinity || this.props.settings.totalRounds === 'Infinity' ? Math.max(this.props.rounds.length, 1) : this.props.settings.totalRounds;
						for (let i = 1; i <= length; i++) entries.push(i);

                        return <CollapsableSection
                            key={'rounds'}
                            value={'rounds'}
                            header={'Rounds'}
                            expanded={this.state.sectionExpando === 'rounds'}
                            setExpanded={(state: boolean, action: string) => this.setState({
                                sectionExpando: state ? action : ''
                            })}
                        >
                           {entries.map(this.renderRound)}
                        </CollapsableSection>						
					})()}
					<SectionBreak />
				</div>
				<div className='bottomBanner'>
					{Bottom([
                        this.props.toggleSettings,
                        <IoMdClose />,
                        'close'
                    ])}
				</div>
			</div>
		);
	}
}