import React, { ReactElement } from 'react';
import { GET } from '../utils/requests';
import { Player, TournamentSettings, ID, History } from '../interfaces';

import styles from '../css/profile.module.css';
import { IoMdClose } from 'react-icons/io';
import { Bottom } from '../components/Buttons';
import CollapsableSection, { SectionBreak } from '../components/CollapsableSection';

export default class Profile extends React.Component<{
	id: string
	playerID: string
	setDisplayPlayer: (id: string) => void
	playerDict: Map<ID, Player>
	modifyPlayerDict: (p: Player, hash: string) => void
	settings: TournamentSettings
	playerDictKey?: string
}, {
	playerID: string
	player: Player
}> {

	public state = {
		playerID: '',
		player: null as Player
	}

	updatePlayerFromProps(refetch?: boolean) {
		this.setState({ playerID: this.props.playerID });		
		if (!this.props.playerID) {
			this.setState({player: null as Player});
			return;
		}
		if (refetch === false) {
			let player = this.props.playerDict.get(this.props.playerID);
			this.setState({ player });
		} else {
			return GET({
				url: '/tournament/' + this.props.id + '/fetchPlayer',
				params: {
					id: this.props.playerID
				}
			})
				.then((player: Player) => {
					if (!player.histories[0]) player.histories[0] = null;
					this.setState({ player });
				});
		}
	}

	private getNameFromPlayer(player: Player): string {
		if (!player) return '\u200b';
		if (this.props.settings.competitors === 'individual') return [player.firstName, player.lastName].join(' ');
		else return player.firstName;
	}

	componentDidUpdate(prevProps: { dataHash?: string, playerDictKey?: string }) {
		if (this.state.playerID !== this.props.playerID) this.updatePlayerFromProps(false);
		else if (this.props.playerDictKey !== prevProps.playerDictKey) this.updatePlayerFromProps(false);
	}

	private getDisplayName(id: ID): string {
        if (!id) return undefined;
		if (id === 'bye') return 'BYE';
		let player = this.props.playerDict.get(id);
		if (!player) return id;
		let displayName: string;
		if (this.props.settings.competitors === 'individual') displayName = [player.firstName, player.lastName].join(' ');
		else displayName = player.firstName;
		return displayName;
    }
    
    renderEntry([k, v]: [string, any], i: number): ReactElement {
        if (k === 'Playing' && v) {
            return (
                <div className={styles.row} key={['row', i].join('.')}>
                    <div className='fieldBox'>{k + ':'}</div>
                    <div
                        className='playerInfo'
                        id={v as string}
                        onClick={(e: React.MouseEvent<HTMLDivElement>) => this.props.setDisplayPlayer((e.target as HTMLDivElement).id)}
                    >{v}</div>
                </div>
            );
        } else {
            return (
                <div className={styles.row} key={['row', i].join('.')}>
                    <div className='fieldBox'>{k + ':'}</div>
                    <div className='fieldBox'>{v ?? '\u200b'}</div>
                </div>
            );
        }
    }

	render(): ReactElement {
        if (!this.state.player) return null;
        let player = this.state.player;
        let Name = this.getNameFromPlayer(player);
		const profile = !player ? {} : {
            'Unique ID': player.id,
            Status: player.active !== false ? 'Active' : 'Inactive',
			Rating: player.rating,
			'Performance rating': player.performanceRating,
			Playing: this.getDisplayName(player.isPlaying)
		};
        

		const total = (Math.max(player.histories.length, 1) - 1 - (player.isPlaying ? 1 : 0)).toString();
		const totalGames = player.histories.slice(1).reduce((acc, curr) => acc += curr.played, 0);
		const scores = !player ? {} : {
			Score: player.score.toString() + ' / ' + total,
			'Game score': player.gamePoints.toString() + ' / ' + totalGames.toString(),
			'Expected score': player.expectedScore.toFixed(2),
			'Expected game score': player.expectedGameScore.toFixed(2) + ' / ' + totalGames,
		};
		if (!this.props.settings.displayPoints) {
			delete scores['Game score'];
			delete scores['Expected game score'];
		}
		let history = player.histories;

		return (
			<div className={styles.sidebar}>
				<div className={styles.profile}>		
					<div className='topBanner'>
						{Name}
					</div>	
					{Object.entries(profile).map(this.renderEntry)}
                    <CollapsableSection
                        header='Score'
                        useInternalState={true}
                        style={{ gridColumn: '1 / span 2' }}
                    >
					    {Object.entries(scores).map(this.renderEntry)}
                    </CollapsableSection>
                    <CollapsableSection
                        header='Results'
                        useInternalState={true}
                        style={{ gridColumn: '1 / span 2' }}
                    >
                        {history.map((minPlayer: History, i: number): ReactElement => {
                            if (i === 0 || !minPlayer) return null;
                            let { id, colour } = minPlayer;
                            let result = minPlayer[this.props.settings.displayPoints];
                            return (
                                <div className={styles.resultsRow} key={['row', 'history', i].join('.')}>
                                    <div className='fieldBox'>{i}</div>
                                    <div
                                        className='playerInfo'
                                        id={id}
                                        onClick={(e: React.MouseEvent<HTMLDivElement>) => this.props.setDisplayPlayer((e.target as HTMLDivElement).id)}
                                    >{this.getDisplayName(id)}</div>
                                    <div className='fieldBox'>{colour || '\u200b'}</div>
                                    <div className='fieldBox'>{typeof result !== 'undefined' && result !== null ? result : '\u200b'}</div>
                                </div>
                            );
                        })}
                    </CollapsableSection>
                    <SectionBreak />
				</div>
				<div className='bottomBanner'>
					{Bottom([() => this.props.setDisplayPlayer(''), <IoMdClose />, 'close'])}
				</div>	
			</div>
		);
	}
}