import React from 'react';
import { win } from './utils/electron';
import { FaWindowMaximize, FaWindowRestore, FaWindowMinimize, FaWindowClose } from 'react-icons/fa';

import styles from './css/statusbar.module.css';

export default class StatusBar extends React.Component<{}, {
	name: string
}> {

	public state = {
		name: ''
	}

	private get maximised(): boolean {		
		return win.isMaximized();
	}

	private minimise = () => {
		win.minimize();
	}

	private close = () => {
		win.close();
	}

	private toggleMaximisation = () => {
		if (this.maximised) {
			win.restore();
			this.forceUpdate();
		} else {
			win.maximize();
			this.forceUpdate();
		}
	}

	render () {
		return (
			<div className={styles.statusBar}>
				<div className={styles.title}>
					Tournament by Oxford Chess Development
				</div>
				<div className={styles.menu}>
					<div className={styles.favicon}>
						<img
							alt='favicon'
							draggable={false}
							src={process.env.PUBLIC_URL + '/logo192.png'}
						/>
					</div>
				</div>
				<div className={styles.exec}>
					<div onClick={this.minimise}>
                        <FaWindowMinimize />
                    </div> 
					<div onClick={this.toggleMaximisation} >
                        {this.maximised ? 
                            <FaWindowRestore /> :
                            <FaWindowMaximize />
                        }
                    </div> 
					<div onClick={this.close} className={styles.close}>
                        <FaWindowClose />
                    </div>
				</div>
			</div>
		);
	}
}