import React, { ReactElement, FormEvent } from 'react';
import { render } from 'react-dom';
import CSV from 'papaparse';
import url from 'url';
import { Player, TournamentPlayers, ID, TournamentStandings, TournamentSettings } from '../interfaces';
import { GET, POST, server, FORM} from '../utils/requests';

import styles from '../css/sidebar.module.css';
import { AiFillEdit, AiOutlineClose, AiOutlineCheck } from 'react-icons/ai';
import { MdRefresh } from 'react-icons/md';
import { IoMdClose } from 'react-icons/io';
import { Bottom } from '../components/Buttons';
import CollapsableSection, { SectionBreak } from '../components/CollapsableSection';

export default class Sidebar extends React.Component<{

	playerDict: Map<ID, Player>
	playerDictKey: string //required to force a reload
	updatePlayerDict: () => void
	modifyPlayerDict: (p: Player) => void

	settings: TournamentSettings
	id: string
	wideLeft: boolean
	setDisplayPlayer: (id: string) => void
	checkIfEnoughPlayers: (reload?: boolean) => void
}, {
	round: number
	players: TournamentPlayers
	linkedState: TournamentStandings
}> {

	public state = {
		round: 0,
		players: {} as TournamentPlayers,
		linkedState: {} as TournamentStandings
	}

	/**
	 * The fundamental method. Called everytime there's a change to the player list. The backbone of this element.
	 */
	public updatePlayerList = async (refresh?: boolean | void): Promise<void> => {
		let { round, players } = refresh !== false ? await this.updatePlayerState() : {round: this.state.round, players: this.state.linkedState};
		let state = {} as TournamentPlayers;
		for (let [k, v] of Object.entries(players)) {
			state[k] = v.map((id: string) => {
				let player = this.props.playerDict.get(id);
				//if (!player) console.error('Unmatched ID: ' + id);
				return player;
			}).sort((a: Player, b: Player) => b.rating - a.rating).filter((v) => v);
		}
		this.setState({
			round,
			players: state
		});
		this.props.checkIfEnoughPlayers(false);
	}

	private updatePlayerState = (): Promise<{round: number, players: TournamentStandings}> => {
		return GET({ url: '/tournament/' + this.props.id + '/getState' })
			.then(({round, players}: {round: number, players: TournamentStandings}) => {
				this.setState({ linkedState: players });
				return {round, players};
			});
	}

	/** EDITING SECTION */

	private get requireLastName() {
		return this.props.settings.competitors === 'individual';
	}

	/**
	 * Allows users to press the 'Escape' key to exit editing an existing player
	 * Returns editing fields back to ReactElement submitted boxes
	 */
	private editToDisplay = (child: HTMLDivElement, player: Player): void => {
		let parent = child as HTMLDivElement;
		while (parent.parentElement) {
			if (parent.id === player.id) break;
			else parent = parent.parentElement as HTMLDivElement;
		}
		let dom = parent.parentElement.cloneNode() as HTMLDivElement;
		render(this.generateActivePlayer(player, 0), dom);
		parent.replaceWith(dom.firstChild);
	}

	/**
	 * Allows users to press the 'Escape' key to exit editing an existing player
	 */
	private editEscape = (e: React.KeyboardEvent<HTMLFormElement>, player: Player): void => {
		const ESCAPE_KEY = 27;
		if (e.keyCode !== ESCAPE_KEY) return;
		this.editToDisplay(e.target as HTMLDivElement, player);
	}

	/**
	 * Triggered whenever a user hits the 'edit' key. Creates Form DOM elements that can be used to send off a post request
	 */
	private editPlayer = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
		let button = e.target as HTMLButtonElement;
		if (button.constructor.name !== 'HTMLButtonElement') return;
		let playerLine = button.parentNode as HTMLDivElement;
		let id = playerLine.id;
		let scoreSection = playerLine.parentNode as HTMLDivElement;
		let score = scoreSection.id.split('.').slice(1).join('.');
		let player = this.state.players[score.toString()].find(p => p.id === id);
		playerLine.innerHTML = '';
		let elem = this.generateForm(id, player.firstName, player.lastName, player.rating, player);
		render(elem, playerLine);
		let child = playerLine.firstChild.firstChild.firstChild as HTMLInputElement;
		child.focus();
	}

	updatePlayer(id: ID): Promise<Player> {
		return GET({
			url: '/tournament/' + this.props.id + '/fetchPlayer',
			params: { id }
		})
			.then((player: Player) => {
				this.props.modifyPlayerDict(player);
				return player;
			});
	}
	
	generateForm(id: string, firstName: string, lastName: string, rating: number, player: Player): JSX.Element {
		return (
			<div key={['scoreSectionEdited', id].join('.')} className={styles.scoreSection}>
				<form
					target='_self'
					onKeyDown={(e) => this.editEscape(e, player)}
					onSubmit={async (e: FormEvent<HTMLFormElement>): Promise<void> => FORM(e)
						.then(this.updatePlayerList)
						.then(async () => {
							let newPlayer = await this.updatePlayer(player.id);
							this.editToDisplay(e.target as HTMLDivElement, newPlayer);
						})
					}
					method='POST'
					action={url.resolve(server, ['tournament', this.props.id, 'editPlayer'].join('/'))}
				>
					<input type='text' className={styles.input} name='firstName' defaultValue={firstName} required style={{width: this.requireLastName ? '25%' : '65%'}} />
					<input type='text' className={styles.input} name='lastName' defaultValue={lastName} required={this.requireLastName} style={{width: '40%', display: this.requireLastName ? 'block' : 'none'}}/>
					<input type='number' className={styles.input} min={0} max={3200} name='rating' defaultValue={rating} required style={{width: '20%'}}/>
					<input type='hidden' name='id' value={id} required/>
					<button tabIndex={-1} className={styles.submit}>
                        <AiOutlineCheck />
                    </button>
				</form>
			</div>
		);
	}

	/** CLEARING SECTION */

	private activatePlayer = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
		let button = e.target as HTMLButtonElement;
		if (button.constructor.name !== 'HTMLButtonElement') return;
		let id = (button.parentNode as HTMLDivElement).id;
		GET({
			url: '/tournament/' + this.props.id + '/activatePlayer',
			params: { id }
		})
			.then(() => this.updatePlayer(id))
			.catch(console.error);
	}

	/**
	 * Clears a single page. Called when the 'X' button is hit next to a player
	 */
	private clearPlayer = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
		let button = e.target as HTMLButtonElement;
		if (button.constructor.name !== 'HTMLButtonElement') return;
		let id = (button.parentNode as HTMLDivElement).id;
		GET({
			url: '/tournament/' + this.props.id + '/clearPlayer',
			params: { id }
		})
			.then(() => this.updatePlayer(id))
			.catch(console.error);
	}

	/**
	 * Clears all players. Called when the 'X' button is pressed at the bottom of the sidebar
	 */
	private clearPlayerList = async (): Promise<void> => {
		await GET({ url: '/tournament/' + this.props.id + '/clearPlayers' }).catch(console.error);
		this.props.checkIfEnoughPlayers();
	}

	/** RENDERING SECTION */

	private handlePaste = (e: React.ClipboardEvent<HTMLInputElement>) => {
		let text = e.clipboardData.getData('text').toString();
		if (!text.trim()) return;
		if (!text.split('\n').length && text.split(',').length && text.split('\t').length) return;
		e.preventDefault();
		let json = CSV.parse(text, {
			delimitersToGuess: [',', '\t'],
			dynamicTyping: true,
			fastMode: true
		} as CSV.ParseConfig) as CSV.ParseResult;
		let data = json.data.map((row) => {
			let [firstName, lastName, rating]: [string, string, number] = row;
			if (!this.requireLastName) [firstName, rating, lastName] = [...row, ''];
			if (!firstName || (!lastName && this.requireLastName) || rating === null || rating === undefined) return null;
			try {
				if (typeof firstName !== 'string') throw [firstName, 'string'].join(',');
				if (typeof lastName !== 'string' && this.requireLastName) throw [lastName, 'string'].join(',');
				if (typeof rating !== 'number') throw [rating, 'number'].join(',');
				return {firstName, lastName, rating};
			} catch (error) {
				if (!text.split('\n').length) return null;
				let e = error.split(',');
				console.error(new TypeError('Bad input on row ' + row.join(',') + '\n' + e[0] + ' is not type ' + e[1]));
				return null;
			}
		})
			.filter(r => r);
		POST({
			url: 'tournament/' + this.props.id + '/appendPlayers',
			data
		})
			.then(() => Promise.all([this.props.updatePlayerDict(), this.updatePlayerList()]));
	}

	generateNewForms(max: number): JSX.Element[] {
		let inputs = new Array(max) as JSX.Element[];
		for (let i = 0; i < max; i++) {
			let key = ['playerForm', i].join('.');
			inputs[i] = (
				<form
					id={['addPlayerForm', i].join('.')}
					ref={key}
					key={key}
					target='_self'
					onSubmit={(e: FormEvent<HTMLFormElement>): Promise<void> => FORM(e)
						.then(() => {
							let form = e.target as HTMLFormElement;
							form.reset();
						})
						.then(() => Promise.all([this.props.updatePlayerDict(), this.updatePlayerList()]))
						.then(() => {
							let first = this.refs['playerForm.0'] as HTMLFormElement;
							let firstInput = first.childNodes[0] as HTMLInputElement;
							firstInput.focus();
						})
					}
					method='POST'
					action={url.resolve(server, ['tournament', this.props.id, 'appendPlayer'].join('/'))}
				>
					<input type='text' className={styles.input} name='firstName' placeholder={this.requireLastName ? 'First name' : 'Team name'} required style={{width: this.requireLastName ? '25%' : '65%'}} onPaste={this.handlePaste}/>
					<input type='text' className={styles.input} name='lastName' placeholder='Last name' required={this.requireLastName} style={{width: '40%', display: this.requireLastName ? 'block' : 'none'}}/>
					<input type='number' className={styles.input} min={0} max={3200} name='rating' placeholder={this.requireLastName ? 'Rating' : 'Av. Rating'} required style={{width: '20%'}}/>
                    <button tabIndex={-1} className={styles.submit}>
                        <AiOutlineCheck />
                    </button>
				</form>
			);
		}
		return inputs;
	}

	private generateActivePlayer = (player: Player, j: number): ReactElement => {
		let displayName = this.requireLastName ? [player.firstName, player.lastName].join(' ') : player.firstName;
		return (
			<div key={['playerLine', j].join('.')} id={player.id} className={styles.playerLine}>
				<div
					className={['playerInfo', styles.playerInfo, !player.active ? styles.playerInactive : ''].join(' ')}
					style={{
						gridColumn: this.state.round && !this.props.settings.allowNewPlayers ? '1 / span 3' : undefined
					}}
					onClick={(e: React.MouseEvent<HTMLDivElement>) => this.props.setDisplayPlayer((e.target as HTMLDivElement).parentElement.id)}
				>
					{displayName} ({player.rating.toString()}) {this.state.round && (player.played < this.state.round) ? '*' : ''}
				</div>
				{this.state.round && !this.props.settings.allowNewPlayers ? null : <>
					<button name='edit' className={styles.edit} onClick={(this.editPlayer)} >
                        <AiFillEdit />
                    </button>
					{player.active ?
						<button name='cancel' className={styles.cancel} onClick={(this.clearPlayer)}>
                            <AiOutlineClose />
                        </button> :
						<button name='active' className={styles.activate} onClick={(this.activatePlayer)}>
                            <AiOutlineCheck />
                        </button>
					}
				</>}
			</div>
		);
	}

	generateActivePlayerList(): ReactElement[] {
		let list = [] as ReactElement[];
		let players = this.state.players;
		for (let i = this.state.round; i >= 0; i-= 0.5) {
			let score = i.toString();
            if (!players[score] || !players[score].length) continue;
            let section = <CollapsableSection
                header={score + ' points'}
                useInternalState={true}
                id={['container', score].join('.')}
            >
                {players[score].map(this.generateActivePlayer)}	
            </CollapsableSection>
            list.push(section);
		}
		return list;
	}

	private prevPropsPlayerDict: Map<ID, Player> = new Map();
	private prevPropsPlayerDictKey: string;
	componentDidUpdate() {
		if (!this.props.playerDict.size) return;
		if (Array.from(this.props.playerDict.keys()).some(k => !this.prevPropsPlayerDict.get(k))) return this.triggerReload();
		if (this.props.playerDictKey !== this.prevPropsPlayerDictKey) return this.triggerReload();
		let currDict = Array.from(this.props.playerDict.values());
		let prevDict = Array.from(this.prevPropsPlayerDict.values());
		if (JSON.stringify(prevDict) !== JSON.stringify(currDict)) return this.triggerReload();
	}
	
	private triggerReload() {
		this.prevPropsPlayerDictKey = this.props.playerDictKey;
		this.updatePlayerList();
		this.prevPropsPlayerDict = this.props.playerDict;
	}

	componentDidMount(): void {
		this.updatePlayerList();
	}

	render(): ReactElement {

		if (this.props.wideLeft) return null;
		let players = this.generateActivePlayerList();
		let inputs = this.generateNewForms(Math.max(1, 10 - Object.values(this.state.players).reduce((acc: number, curr: Player[]) => acc += curr.length, 0)));

		return (					
			<div className={styles.sidebar} >
				<div className={styles.topBanner}>
					Tournament Players
				</div>	
				<div className={styles.playersSection}>
					{!players.length ? <div className={styles.scoreSection}>
						<div className={styles.playerLine}>
							<div className={styles.sectionHeader}>
								{players.length ? 'Player List' :'No players added yet'}
							</div>
						</div>
					</div> : null}
					{players}
					{this.state.round && !this.props.settings.allowNewPlayers ?
                        <div className={styles.scoreSection}>
                            {inputs}
                        </div>
                    : null}
					<SectionBreak />
				</div>
				<div className='bottomBanner'>
					{Bottom(
                        [async () => {
                            this.props.updatePlayerDict();
                            this.updatePlayerList();
                        }, <MdRefresh />, 'refresh'],
                        this.state.round === 0 ? [async () => {
                            for (let form of Object.values(this.refs)) {
                                if (form.constructor.name !== 'HTMLFormElement') continue;
                                (form as HTMLFormElement).reset();
                            }
                            if (this.state.round !== 0) return;
                            await this.clearPlayerList();
                            //this.props.updatePlayerDict();
                            this.updatePlayerList();
                        }, <IoMdClose />, 'close'] : null
                    )}
				</div>
			</div>
		);
	}
}