import React, { ReactElement } from 'react';
import { server, POST } from '../utils/requests';
import url from 'url';
import styles from '../css/tournament.module.css';
import { TournamentStatus, TournamentSettings } from '../interfaces';
import { EditableField } from '../components/';
import { MdExitToApp, MdStop } from 'react-icons/md';
import { IoMdPlay, IoMdGrid } from 'react-icons/io';

export default class InfoBar extends React.Component<{
	status: TournamentStatus
	settings: TournamentSettings
	updateTournamentStatus: () => void
	wide: boolean
	wideLeft: boolean
	round: number
	totalRounds: number | 'Infinity'
	closeTournament: () => void
	toggleCrossTable: () => void

	lastRoundOverride: boolean
	setLastRound: (override: boolean) => void
	triggerError: (message: string) => void
}> {

	render(): ReactElement {
		//let i = 0;
		//if (this.props.wide) i++;
		//if (this.props.wideLeft) i++;

		let lastRound = false;
		if (typeof this.props.lastRoundOverride !== 'undefined') lastRound = this.props.lastRoundOverride;
		else lastRound = this.props.settings.totalRounds === this.props.status.round;

		let uri = url.resolve(server, ['tournament', this.props.status.id, 'setName'].join('/'));

		return (
			<div className={styles.titleBar} style={{
				gridColumn: '2 /span 1'//(this.props.wideLeft ? '1' : '2') + ' / span ' + (1 + i).toString()
			}}>
				<div className={styles.row}>
					<div className={['topBanner', styles.header].join(' ')}>
						<EditableField
							content={this.props.status.name}
							url={uri}
							inputType='text'
							handleSubmit={(k: string, v: any) => POST({
								url: uri,
								data: {
									[k]: v
								}
							})}
							additionalStyles={styles}
							additionalInputProps={{}}
							onSubmitSuccess={(e) => {
								this.props.updateTournamentStatus();
								return e;
							}}
							defaultContent='Unnamed Swiss Tournament'
						/>
					</div>
				</div>
				<div className={styles.row}>
					<div className={'topBanner ' + styles.header}>
						ID: {this.props.status.id}
					</div>
					<div className={'topBanner ' + styles.header}>
						Round: {!this.props.status.round ? 'Not yet initialised' : this.props.status.round + ' / ' + (this.props.totalRounds === 'Infinity' ? '' : this.props.totalRounds)}
					</div>
				</div>
				<div className={styles.row}>
					<div className='button' onClick={this.props.closeTournament}>
						<span role='img' aria-label='back'>
                            <MdExitToApp />
                        </span>
					</div>
					<div className='button' onClick={() => {
						if (this.props.status.round >= this.props.settings.totalRounds) this.props.triggerError('Increase the number of rounds in Settings to continue!')
						this.props.setLastRound(!lastRound);
					}}>
						<span role='img' aria-label='decommission'>
                            {lastRound ? 
                                <IoMdPlay /> :
                                <MdStop />
                            }
						</span>
					</div>
					<div className='button' onClick={this.props.toggleCrossTable}>
						<IoMdGrid />
					</div>
				</div>
			</div>
		);
	}
}